<template>
    <page-wrap>

        <!--  -->
        <page-title title="-"/>

        <!--  -->
        <search-wrap>
            <el-form ref="search-form" label-width="70px" :model="searchData" size="mini">
                <el-row :gutter="20">
                    <el-col :sm="7" :md="6" :lg="5">
                        <div class="bg">
                            <el-form-item label="">
                                <el-input v-model="searchData.keyword" placeholder=""></el-input>
                            </el-form-item>
                        </div>
                    </el-col>

                    <el-col :sm="7" :md="6" :lg="5">
                        <div class="bg">
                            <el-form-item label="">
                                <el-input class="bg_button" v-model="searchData.filterClass.value"
                                          @click.native="handleOpenFilterClass" placeholder=""
                                          readonly></el-input>
                            </el-form-item>
                        </div>
                    </el-col>

                    <el-col :sm="6" :md="6" :lg="5">
                        <div class="bg">
                            <el-form-item label="" label-width="82px">
                                <el-input class="bg_button"
                                          @click.native="handleOpenFilterAssess" placeholder=""
                                          readonly></el-input>

                                <!-- <el-select v-model="searchData.assess" placeholder="">
                                 <el-option
                                   v-for="item in assessOptions"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value">
                                 </el-option>
                               </el-select> -->
                            </el-form-item>
                        </div>
                    </el-col>

                    <el-col :sm="24" :md="24" :lg="9">
                        <div class="bg">
                            <el-form-item label="" label-width="90px">
                                <el-input class="bg_button" v-model="searchData.filterClass.value"
                                          @click.native="openTraceCompare = true" placeholder=""
                                          readonly></el-input>
                            </el-form-item>
                        </div>
                    </el-col>

                </el-row>

                <el-row :gutter="20">

                    <el-col :sm="7" :md="6" :lg="5">
                        <div class="bg">
                            <el-form-item label="">
                                <el-input @click.native="openFilterIndex = true" class="bg_button"
                                          placeholder="" readonly></el-input>
                            </el-form-item>
                        </div>
                    </el-col>

                    <el-col :sm="7" :md="6" :lg="5">
                        <div class="bg">
                            <el-form-item label="" label-width="82px">
                                <el-cascader
                                        :options="dp_user_name"
                                        :show-all-levels="false"
                                        v-model="searchData.dp_user_id"
                                        change-on-select
                                        @change="handleChange">
                                </el-cascader>
                            </el-form-item>
                        </div>
                    </el-col>

                    <el-col :sm="7" :md="9" :lg="9">
                        <div class="bg">
                            <el-form-item label="" label-width="90px">
                                <el-date-picker
                                        v-model="searchData.dateTime"
                                        type="daterange"
                                        value-format="yyyy-MM-dd"
                                        range-separator=""
                                        start-placeholder=""
                                        end-placeholder="">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                    </el-col>

                </el-row>

                <el-row :gutter="20">
                    <el-col :span="10">
                        <div class="bg">
                            <!--<el-form-item label="">-->
                            <!--<el-input placeholder="" @click.native="handleOpenFilterTags" class="bg_button"-->
                            <!--readonly></el-input>-->
                            <!--</el-form-item>-->
                            <el-form-item label="">
                                <el-radio-group v-model="radio_group">
                                    <el-radio :label="2"></el-radio>
                                    <el-radio :label="1"></el-radio>
                                </el-radio-group>&nbsp;&nbsp;
                                <el-select
                                        v-model="searchData.value9"
                                        multiple
                                        filterable
                                        remote
                                        reserve-keyword
                                        placeholder=""
                                        :remote-method="remoteMethod"
                                        :loading="loading_search">
                                    <el-option
                                            v-for="item in options4"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="14">
                        <el-form-item label-width="0px">
                            <el-button type="primary" @click="onSubmit"></el-button>
                            <el-button type="danger" @click="onClear"></el-button>
                            <el-button type="primary" plain @click="handleOpenAddKeyword"
                                       :disabled="!buttonControl.lexiconkeywordsadd">
                            </el-button>
                            <el-button type="success" plain @click="handleOpenImportKeyword"
                                       :disabled="!buttonControl.lexiconimport">
                            </el-button>
                            <el-button type="danger" plain @click="openTraceSet = true" :disabled="!buttonControl.lexiconopponentlist"></el-button>
                            <el-button type="warning" plain @click="getTagsInfo"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </search-wrap>

        <!--  -->
        <table-wrap>
            <div class="withTableHeader" style="width: 98.8%;">
                <el-button type="primary" plain size="mini" @click="handleOpenEditKeywordBatch"
                           :disabled="!buttonControl.lexiconkeywordseditbench">
                </el-button>
                <el-badge :value="moreTableColsNumber" class="moreTableCols">
                    <el-button size="mini" @click="handleMoreTableCols"></el-button>
                    <div v-show="showMoreTableCols" id="moreTableCols">
                        <el-checkbox-group v-model="hiddenTableCols" @change="selectHiddenTableCols">
                            <el-checkbox label="kws_count_pc">PC</el-checkbox>
                            <el-checkbox label="kws_count_m">M</el-checkbox>
                            <el-checkbox label="baidu_rank_pc">PC</el-checkbox>
                            <el-checkbox label="baidu_rank_m">M</el-checkbox>
                            <el-checkbox label="trace_rank_pc">PC</el-checkbox>
                            <el-checkbox label="trace_rank_m">M</el-checkbox>
                            <el-checkbox label="baidu_index_pc">PC</el-checkbox>
                            <el-checkbox label="baidu_index_m">M</el-checkbox>
                            <el-checkbox label="news_index">PC</el-checkbox>
                            <el-checkbox label="mnews_index">M</el-checkbox>
                            <el-checkbox label="course_index">PC</el-checkbox>
                            <el-checkbox label="mcourse_index">M</el-checkbox>
                            <el-checkbox label="school_index">PC</el-checkbox>
                            <el-checkbox label="mschool_index">M</el-checkbox>
                            <el-checkbox label="zhidao_index">PC</el-checkbox>
                            <el-checkbox label="mzhidao_index">M</el-checkbox>
                        </el-checkbox-group>
                    </div>
                </el-badge>
            </div>
            <el-table
                    ref="table"
                    v-loading="loading"
                    tooltip-effect="dark"
                    :data="tableData"
                    @select="selectTableRow"
                    @select-all="selectTableAllRow"
                    @sort-change="tableSortChange"
                    size="mini"
                    max-height="550">
                <el-table-column fixed="left" type="selection" width="55px"></el-table-column>
                <el-table-column fixed="left" prop="keyword" :show-overflow-tooltip="isTrue" width="220px" label="">
                    <template slot-scope="props">
                        <a @click="clickKeyword(props.row.keyword)">{{props.row.keyword}}</a>
                    </template>
                </el-table-column>
                <el-table-column prop="catename" width="120px" show-overflow-tooltip label=""></el-table-column>
                <!-- <el-table-column prop="tags" label="" v-show="showTableColTags"></el-table-column> -->

                <el-table-column prop="score_pc" sortable="custom" width="110px" label="PC">
                    <template slot-scope="props">
                        <span v-show="props.row.score_pc == 1" class="check_green"></span>
                        <span v-show="props.row.score_pc == 2" class="check_orange"></span>
                        <span v-show="props.row.score_pc == 3" class="check_orange"></span>
                        <span v-show="props.row.score_pc == 4" class="check_red"></span>
                        <span v-show="props.row.score_pc == 5" class="check_red"></span>
                        <span v-show="props.row.score_pc == 0" class="check_gray"></span>
                    </template>
                </el-table-column>

                <el-table-column prop="score_m" sortable="custom" width="110px" label="M">
                    <template slot-scope="props">
                        <span v-show="props.row.score_m == 1" class="check_green"></span>
                        <span v-show="props.row.score_m == 2" class="check_orange"></span>
                        <span v-show="props.row.score_m == 3" class="check_orange"></span>
                        <span v-show="props.row.score_m == 4" class="check_red"></span>
                        <span v-show="props.row.score_m == 5" class="check_red"></span>
                        <span v-show="props.row.score_m == 0" class="check_gray"></span>
                    </template>
                </el-table-column>

                <el-table-column prop="kws_count_pc" v-if="hiddenTableColsBool.kws_count_pc" sortable="custom" width="120px" label="PC"></el-table-column>
                <el-table-column prop="kws_count_m" v-if="hiddenTableColsBool.kws_count_m" sortable="custom" width="110px" label="M"></el-table-column>
                <el-table-column prop="baidu_rank_pc" v-if="hiddenTableColsBool.baidu_rank_pc" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="baidu_rank_m" v-if="hiddenTableColsBool.baidu_rank_m" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="trace_rank_pc" v-if="hiddenTableColsBool.trace_rank_pc" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="trace_rank_m" v-if="hiddenTableColsBool.trace_rank_m" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="baidu_index_pc" v-if="hiddenTableColsBool.baidu_index_pc" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="baidu_index_m" v-if="hiddenTableColsBool.baidu_index_m" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="news_index" v-if="hiddenTableColsBool.news_index" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="mnews_index" v-if="hiddenTableColsBool.mnews_index" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="course_index" v-if="hiddenTableColsBool.course_index" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="mcourse_index" v-if="hiddenTableColsBool.mcourse_index" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="school_index" v-if="hiddenTableColsBool.school_index" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="mschool_index" v-if="hiddenTableColsBool.mschool_index" sortable="custom" width="130px" label="M"></el-table-column>
                <el-table-column prop="zhidao_index" v-if="hiddenTableColsBool.zhidao_index" sortable="custom" width="130px" label="PC"></el-table-column>
                <el-table-column prop="mzhidao_index" v-if="hiddenTableColsBool.mzhidao_index" sortable="custom" width="130px" label="M"></el-table-column>

                <el-table-column prop="user_name" label=""></el-table-column>
                <el-table-column prop="add_time" width="160px" label=""></el-table-column>
                <el-table-column label="" header-align="center" fixed="right" width="400px">
                    <template slot-scope="scope">
                        <!-- <el-button type="text" plain size="mini" icon="el-icon-view" @click="changeKeyWord(scope.row.keyword)"></el-button> -->
                        <el-button type="text" plain size="mini" icon="el-icon-view"
                                   @click="handleOpenTrend(scope.row.keyword)">
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-edit"
                                   @click="handleOpenEditKeyword(scope.row)"
                                   :disabled="!buttonControl.lexiconkeywordsedit">
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-refresh"
                                   @click="refresh(scope.row.keyword)"
                                   :disabled="!buttonControl.lexiconrefresh || scope.row.status !== 2">
                            
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-star-off"
                                   @click="getschoollabel(scope.row.keyword)"
                                   :disabled="!buttonControl.lexiconrefresh">
                            
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-delete"
                                   @click="onDelete(scope.row)">
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </table-wrap>


        <!--  -->
        <pagination-wrap>
            <new-pagination
                    :total="paginationData.total"
                    :propCurrentPage="paginationData.currentPage"
                    @initPaginationData="showPaginationData"
                    @handleSizeChange="showSizeChange"
                    @handleCurrentChange="showCurrentChange"
            />
        </pagination-wrap>

        <!-- dialog -->
        <el-dialog title="" width="500px" :visible.sync="openTagConfiguration" @close="initTagsOperateData">

            <el-dialog
                    width="250px"
                    title="/"
                    :visible.sync="openEditTagConfiguration"
                    @close="handleEditTagCancel"
                    append-to-body>
                <el-input v-model="tagsOperateData.tagName" size="mini"></el-input>
                <div slot="footer">
                    <el-button size="mini" @click="handleEditTagCancel"> </el-button>
                    <el-button size="mini" type="primary" @click="handleEditTagConfirm"> </el-button>
                </div>
            </el-dialog>

            <el-form ref="tag-form" :model="tagsOperateData" size="mini">
                <el-form-item>
                    <el-input v-model="tagsOperateData.searchedTag" prefix-icon="el-icon-search"
                              placeholder="" style="width:200px;margin-right: 30px;"></el-input>
                    <el-button type="warning" plain @click="handleSearchTag"></el-button>
                    <el-button type="success" plain @click="handleAddTag"></el-button>
                </el-form-item>
            </el-form>

            <el-table
                    :data="tagsData"
                    size="mini"
                    :height="170">
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="props">
                        <el-button type="text" plain size="mini" icon="el-icon-edit"
                                   @click="handleEditTag($event, props.row.id, props.row.name)">
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-delete"
                                   @click="handleDeleteTagConfirm($event, props.row.id)">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <div slot="footer">
                <el-pagination
                        small
                        layout="prev, pager, next"
                        @current-change="changeCurrent"
                        :pageSize="tagsPaginationData.pageSize"
                        :total="tagsPaginationData.total">
                </el-pagination>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog title="" width="500px" :visible.sync="openSchoolLabel" @close="cloaseSchoolLabel">
           <el-tag type="warning" v-show="schooltagsData.length === 0"><i class="el-icon-warning">&nbsp;</i> </el-tag>
           <template v-for="(item, index) in schooltagsData">
               <el-tag style="margin: 0 10px 10px 0;" type="success">{{item.name}}</el-tag>
           </template>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="500px"
                title=""
                @close="handleImportCancel"
                :visible.sync="openImport">
            <el-form ref="form" label-width="100px">
                <el-form-item>
                    <el-tag
                            v-show="keywordOperateData.edit.id == -1"
                            size="medium"
                            style="margin-left: 15px;"
                            type="warning">
                        <i class="el-icon-info"></i>
                        {{keywordNum}}
                    </el-tag>
                </el-form-item>
                <el-form-item label="" required>
                    <el-input class="bg_button" style="width:215px;" v-model="searchData.filterClass.value"
                              @click.native="handleOpenFilterClass" placeholder="" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select
                            v-model="importLabel1.value9"
                            multiple
                            filterable
                            remote
                            reserve-keyword
                            placeholder=""
                            :remote-method="remoteMethodImport1"
                            :loading="loading">
                        <el-option
                                v-for="item in importLabel1.options4"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select
                            v-model="importLabel2.value9"
                            multiple
                            filterable
                            remote
                            reserve-keyword
                            placeholder=""
                            :remote-method="remoteMethodImport2"
                            :loading="loading">
                        <el-option
                                v-for="item in importLabel2.options4"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-upload class="upload-demo" action="index.php/lexicon/import" :data="searchData.filterClass"
                               :before-upload="beforeUpload" :on-success="onSuccess">
                        <el-button size="small" type="primary" @click="chooseFile"></el-button>
                        <div slot="tip" class="el-upload__tip"></div>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="mini" type="primary" @click="handleImportConfirm"></el-button>
                <el-button size="mini" @click="handleImportCancel"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="550px"
                title=""
                @close="handleFilterTagsCancel"
                :visible.sync="openFilterTags">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAllTags" @change="handleCheckAllTags">
            </el-checkbox>
            <div style="margin: 20px 0;"></div>
            <el-checkbox-group v-model="copyFilterTags" @change="initFilterTagsStatus">
                <el-checkbox v-for="tag in tagsData" :label="tag.id" :key="tag.id">{{tag.name}}</el-checkbox>
            </el-checkbox-group>
            <div slot="footer">
                <el-button size="mini" type="primary" plain @click="handleFilterTagsClear"> </el-button>
                <el-button size="mini" @click="handleFilterTagsCancel"> </el-button>
                <el-button size="mini" type="primary" @click="handleFilterTagsConfirm"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="800px"
                class="index-form-style"
                title=""
                @close="handleFilterIndexCancel"
                :visible.sync="openFilterIndex">

            <el-form ref="index-form" size="mini">
                <el-form-item>
                    <el-checkbox-group v-model="copyFilterIndexArray">
                        <template v-for="items in copyFilterIndex">
                            <el-checkbox :label="items.label" :key="items.value">
                                <span>{{items.label}}</span>
                                <el-select v-model="items.type" placeholder="" size="mini">
                                    <el-option
                                            v-for="item in mediaType"
                                            :key="item"
                                            :label="item"
                                            :value="item">
                                    </el-option>
                                </el-select>
                                <el-select v-model="items.character" placeholder="">
                                    <el-option
                                            v-for="item in characterGather"
                                            :key="item"
                                            :label="item"
                                            :value="item">
                                    </el-option>
                                </el-select>
                                <el-input v-model="items.number" placeholder="" style="width: 180px;"></el-input>
                            </el-checkbox>
                            <br/>
                        </template>
                    </el-checkbox-group>
                </el-form-item>
            </el-form>

            <div slot="footer">
                <el-button size="mini" type="primary" plain @click="handleFilterIndexClear"> </el-button>
                <el-button size="mini" @click="handleFilterIndexCancel"> </el-button>
                <el-button size="mini" type="primary" @click="handleFilterIndexConfirm"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="835px"
                title=""
                class="class-list-dialog"
                @close="handleFilterClassCancel"
                :visible.sync="openFilterClass">
            <div class="class-list clearfix">
                <ul class="one">
                    <li class="all"></li>
                    <li class="a">0-18</li>
                    <li class="b"></li>
                    <li class="c"></li>
                </ul>
                <ul class="two">
                    <li class="all"></li>
                    <template v-for="item in classList">
                        <li :key="item.id" :id="item.id" :title="item.value"
                            :class="[item.value === showClassDetail ? 'selectClassTypeTrue' : 'selectClassTypeFalse']"
                            @click="handleHoverClass(item.value)">
                            {{item.value}}
                            <span class="triggle" v-show="item.value === showClassDetail"></span>
                        </li>
                    </template>
                </ul>
                <template v-for="items in classList">
                    <ul class="three" v-show="items.value === showClassDetail">
                        <li class="all">{{items.value}}</li>
                        <template v-for="item in items.children">
                            <li :title="item.value">
                                <span class="left" :key="item.id" :id="item.id"
                                      :class="[item.id === copyFilterClass.id ? 'selectClassOneTrue' : 'selectClassOneFalse']"
                                      @click="handleSelectClass(item)">{{item.value}}</span>
                                <ul class="right">
                                    <template v-for="i in item.children">
                                        <li :title="i.value" :key="i.id" :id="i.id"
                                            :class="[i.id === copyFilterClass.id ? 'selectClassTwoTrue' : 'selectClassTwoFalse']"
                                            @click="handleSelectClass(i)">{{i.value}}
                                        </li>
                                    </template>
                                </ul>
                            </li>
                        </template>
                    </ul>
                </template>
            </div>
            <div slot="footer">
                <el-button size="mini" @click="handleFilterClassCancel"> </el-button>
                <el-button size="mini" type="primary" @click="handleFilterClassConfirm"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="400px"
                title="/"
                @close="handleAddKeywordCancel"
                :visible.sync="openEditKeyword">

            <el-form ref="keywordOperateData" :model="keywordOperateData" :rules="keywordOperateRule" size="small"
                     label-width="80px">
                <el-form-item>
                    <el-tag
                            v-show="keywordOperateData.edit.id == -1"
                            size="medium"
                            style="margin-left: 15px;"
                            type="warning">
                        <i class="el-icon-info"></i>
                        {{keywordNum}}
                    </el-tag>
                </el-form-item>
                <el-form-item prop="keyword" label="" required>
                    <el-input v-model="keywordOperateData.keyword"
                              :readonly="keywordOperateData.edit.id != -1"></el-input>
                </el-form-item>

                <el-form-item prop="class" label="" :rules="[{ required: true, message: ''},]">
                    <el-input class="bg_button" v-model="keywordOperateData.class" prefix-icon="el-icon-search"
                              @click.native="handleOpenKeywordFilterClass"></el-input>
                </el-form-item>

                <!--<el-form-item label="">-->
                    <!--<el-input class="bg_button" prefix-icon="el-icon-search" @click.native="handleOpenKeywordFilterTags"-->
                              <!--readonly></el-input>-->
                <!--</el-form-item>-->

                <el-form-item label="" label-width="100px">
                    <el-radio-group v-model="radio_edit_add">
                        <el-radio :label="2"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-select class="bg_button"
                               v-model="value8"
                               multiple
                               filterable
                               remote
                               reserve-keyword
                               placeholder=""
                               :remote-method="remoteMethod_edit_add"
                               :loading="loading_search">
                        <el-option
                                v-for="item in options3"
                                :key="item.value"
                                :label="item.label"
                                :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <div slot="footer">
                <el-button size="mini" @click="handleAddKeywordCancel"> </el-button>
                <el-button size="mini" type="primary" @click="handleAddKeywordConfirm"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <el-dialog
                width="400px"
                title=""
                @close="handleBatchEditKeywordCancel"
                :visible.sync="openBatchEditKeyword">

            <el-form size="small" label-width="80px">
                <el-form-item prop="class" label="">
                    <el-input class="bg_button" v-model="keywordOperateData.batchEdit.class.value"
                              prefix-icon="el-icon-search" @click.native="handleOpenKeywordBatchFilterClass"
                              readonly></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input class="bg_button" prefix-icon="el-icon-search"
                              @click.native="handleOpenKeywordBatchFilterTags" readonly></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer">
                <el-button size="mini" @click="handleBatchEditKeywordCancel"> </el-button>
                <el-button size="mini" type="primary" @click="handleBatchEditKeywordConfirm"> </el-button>
            </div>
        </el-dialog>
        <!-- dialog -->

        <!-- dialog -->
        <dialog-trace-set
          :show="openTraceSet"
          :click="openTraceSet"
          @close="handleTraceSetClose"
        />
        <!-- dialog -->


        <!-- dialog -->
        <dialog-trace-compare
          :show="openTraceCompare"
          :click="openTraceCompare"
          @close="handleTraceCompareClose"
        />
        <!-- dialog -->

        <!-- dialog- -->
        <dialog-trend
          :show="openTrendDialog"
          :keyword="keywordToTrend"
          @close="handleTrendClose"
          :click="openTrendDialog"
        />
        <!-- dialog- -->

        <!-- dialog -->
        <dialog-assess
          :show="openFilterAssess"
          @close="handleFilterAssessClose"
          @onSubmit="handleFilterAssessonSubmit"
        />
        <!-- dialog -->

    </page-wrap>
</template>


<script>
    import PageWrap from './../../components/pageStructure/PageWrap.vue'
    import SearchWrap from './../../components/pageStructure/SearchWrap.vue'
    import TableWrap from './../../components/pageStructure/TableWrap.vue'
    import paginationWrap from './../../components/pageStructure/paginationWrap.vue'
