<template>
  <el-dialog
    title=""
    :visible.sync="isShow"
    width="70%"
    :before-close="handleClose">
    <table class="table1" cellspacing="0">
      <tr>
        <td colspan="3">
          <b></b>
          <b></b>
          <b></b>14
        </td>
      </tr>
      <tr>
        <td style="width: 15%;"></td>
        <td style="width: 70%;">
          <el-input style="width: 50%;" size="mini"></el-input>
          <span class="red fr" style="margin-right:10px;margin-top: 5px;"></span>
        </td>
        <td rowspan="5" style="width: 15%;" class="buttons">
          <el-button type="primary" plain size="mini">HTML</el-button>
          <el-button type="primary" plain size="mini"></el-button>
          <el-button type="primary" plain size="mini"></el-button>
          <el-button type="primary" plain size="mini"></el-button>
          <el-button type="primary" plain size="mini"></el-button>
          <el-button type="primary" plain size="mini"></el-button>
          <el-button type="primary" plain size="mini"></el-button>
        </td>
      </tr>
      <tr>
        <td></td>
        <td>
          <el-input style="width: 50%;" size="mini"></el-input>
        </td>
      </tr>
      <tr style="height: 60px;">
        <td></td>
        <td>
          <el-upload
            style="margin-top: 10px;"
            action="https://jsonplaceholder.typicode.com/posts/">
            <el-button size="small" type="primary"></el-button>
            <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
          </el-upload>
        </td>
      </tr>
      <tr style="height: 150px;">
        <td></td>
        <td>
          <!--  -->
          <script id="editor" type="text/plain" style="width:200px;height:140px;"></script>
        </td>
      </tr>
      <tr style="height: 50px;">
        <td></td>
        <td>
          <p class="red">2%;</p>
          <p class="red">80%;</p>
        </td>
      </tr>
      <tr style="height: 60px;">
        <td colspan="3">
          <el-radio-group v-model="typeValue" @change="changeTypeValue">
           <el-radio label="xinwen"></el-radio>
           <el-radio label="toutiao"></el-radio>
           <el-radio label="zhidao"></el-radio>
          </el-radio-group>

          <el-form label-position="right">
            <el-form-item label="" label-width="70px" v-show="typeValue === 'xinwen'">
              <el-input size="mini" style="width: 120px;" v-model="area"></el-input>
            </el-form-item>
            <el-form-item label="" label-width="100px" v-show="typeValue === 'zhidao'">
              <el-select v-model="integral" style="width: 100px;" size="mini" placeholder="">
                <el-option
                  v-for="item in integralOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>&nbsp;&nbsp;()
            </el-form-item>
          </el-form>
        </td>
      </tr>
      <tr>
        <td colspan="3" class="buttons2">
          <el-button type="primary" size="mini"></el-button>
          <el-button type="primary" size="mini"></el-button>
          <el-button type="primary" size="mini"></el-button>
          <el-button type="primary" size="mini"></el-button>
          <el-button type="primary" size="mini"></el-button>
        </td>
      </tr>

    </table>
  </el-dialog>

</template>

<script>
  export default {
    props: {
      show: false
    },
    data () {
      return {
        isShow: false,
        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
        typeValue: 'xinwen',
        area: '',
        integral: '',
        integralOptions: [
         {
            value: '0',
            label: ''
          }, {
            value: '10',
            label: '10'
          }, {
            value: '20',
            label: '20'
          }, {
            value: '30',
            label: '30'
          }, {
            value: '40',
            label: '40'
          }, {
            value: '50',
            label: '50'
          }, {
            value: '70',
            label: '70'
          }, {
            value: '100',
            label: '100'
          }
        ],
        editor: null,
        content: '',
        newContent: ''
      }
    },
    watch: {
      show () {
        this.isShow = this.show
      }
    },
    mounted() {
      this.editor = UE.getEditor('editor')
    },
    destroyed () {
      this.editor.destroy()
    },
    methods: {
      getEditorContent () {
        console.log('this.editor.getContent', this.editor.getContent())
      },
      handleClose () {
        this.isShow = false
        this.$emit('close', false)
      },
      changeTypeValue (val) {
        console.log('val', val)
      }
    }
  }
</script>



