<template>
  <div style="display: inline-block;margin-right: 10px;">
    <el-radio-group v-model="dayMark" @change="changeDayMark">
      <el-radio-button label="" v-show="dates.indexOf('') > -1"></el-radio-button>
      <el-radio-button label="" v-show="dates.indexOf('') > -1"></el-radio-button>
      <el-radio-button label="7" v-show="dates.indexOf('7') > -1"></el-radio-button>
      <el-radio-button label="30" v-show="dates.indexOf('30') > -1"></el-radio-button>
    </el-radio-group>

    <template v-if="hasDateRange">
      <el-date-picker
          v-model="dateTime"
          type="daterange"
          value-format="yyyy-MM-dd"
          @change="changeDateTime"
          align="left"
          unlink-panels
          :picker-options="pickerOptions"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
      </el-date-picker>

      <el-checkbox v-show="hasCompare" v-model="compare" @change="changeCompare"></el-checkbox>

      <el-date-picker
          v-model="dateTime2"
          v-show="hasCompare && compare"
          type="daterange"
          value-format="yyyy-MM-dd"
          @change="changeDateTime2"
          align="left"
          unlink-panels
          :picker-options="pickerOptions"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
      </el-date-picker>
    </template>

    <template v-else>
      <el-date-picker
        v-model="datetime"
        type="date"
        placeholder="">
      </el-date-picker>
    </template>

  </div>

</template>

<script>
import { getDates, getDateRange }  from './../../assets/js/baseFunc/baseSelfFunc'

export default {
  props: {
    dates: {
      type: String,
      default: ['', '', '7']
    },
    hasDateRange: {
      type: Boolean,
      default: true
    },
    hasCompare: {
      type: Boolean,
      defallt: false
    },
    reset: {
      type: Boolean
    }
  },
  data () {
    return {
      dayMark: '',
      dateTime: [getDates('yesterday'), getDates('yesterday')],
      dateTime2: [getDates('daybefore'), getDates('daybefore')],
      showDateTime2: false,
      compare: false,
      pickerOptions: {}
    }
  },
  watch: {
    reset () {
      this.dayMark = ''
      this.changeDayMark('')
    }
  },
  created () {
    this.pickerOptions = {
      shortcuts: [
         {
           text: '',
           onClick(picker) {
             let start = new Date()
             picker.$emit('pick', [start, start])
           }
         },
         {
           text: '',
           onClick(picker) {
             let start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 1)
             picker.$emit('pick', [start, start])
           }
         },
         // {
         //   text: '',
         //   onClick(picker) {
         //     let start = new Date()
         //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 2)
         //     picker.$emit('pick', [start, start])
         //   }
         // },
         {
           text: '7',
           onClick(picker) {
             const end = new Date()
             const start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 6)
             picker.$emit('pick', [start, end])
           }
         },
         {
           text: '30',
           onClick(picker) {
             const end = new Date()
             const start = new Date()
             start.setTime(start.getTime() - 3600 * 1000 * 24 * 29)
             picker.$emit('pick', [start, end])
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', getDateRange(''))
           }
         },
         {
           text: '',
           onClick(picker) {
             picker.$emit('pick', ['', ''])
           }
         }
      ]
    }
    this.listenChange()
  },
  methods: {
    changeDayMark (val) {
      switch (val) {
        case '':
            this.dateTime = [getDates('daybefore'), getDates('daybefore')]
            this.dateTime2 = [getDates('fourDay'), getDates('fourDay')]
            break
        case '':
            this.dateTime = [getDates('yesterday'), getDates('yesterday')]
            this.dateTime2 = [getDates('daybefore'), getDates('daybefore')]
            break
        case '7':
            this.dateTime = [getDates('sevenDay'), getDates('today')]
            this.dateTime2 = [getDates('fourteenDay'), getDates('eightDay')]
            break
        case '30':
            this.dateTime = [getDates('thirtyDay'), getDates('today')]
            this.dateTime2 = [getDates('sixtyDay'), getDates('thirty-one-Day')]
            break
      }
      this.listenChange()
    },
    changeCompare () {
      this.listenChange()
    },
    changeDateTime (val) {
      if (val !== null) {
        if (val[0] === getDates('yesterday') && val[1] === getDates('yesterday')) {
          this.dayMark = ''
        } else if (val[0] === getDates('sevenDay') && val[1] === getDates('today')) {
          this.dayMark = '7'
        } else if (val[0] === getDates('thirtyDay') && val[1] === getDates('today')) {
          this.dayMark = '30'
        } else {
          this.dayMark = ''
        }
      } else {
        this.dayMark = ''
      }
      this.listenChange()
    },
    changeDateTime2 () {
      this.listenChange()
    },
    listenChange () {
      if (this.hasCompare) {
        this.$emit('receiveChange', {compare: this.compare, dateTime: this.dateTime, dateTime2: this.compare ? this.dateTime2 : null})
      } else {
        this.$emit('receiveChange', {dateTime: this.dateTime})
      }
    }
  }
}
</script>



