<template>
    <page-wrap>

        <!--  -->
        <page-title title="-" />

        <!--  -->
        <search-wrap>
          <el-form ref="search-form" label-width="70px" :model="searchData" size="mini">
              <el-row :gutter="20">
                  <el-col :span="24">
                      <el-form-item label="">
                        <el-input v-model="searchData.keyword" style="margin-right: 10px;" @keyup.enter.native="onSubmit"></el-input>

                        <el-tag
                          type="success"
                          size="medium"
                          style="margin-right: 10px;"
                          :key="tag"
                          v-for="tag in searchData.comapredKeyword"
                          closable
                          :disable-transitions="false"
                          @close="closeCompareKeyword(tag)">
                          {{tag}}
                        </el-tag>

                        <el-input
                          v-if="addKeywordInputVisible"
                          v-model="addKeywordInputValue"
                          ref="saveTagInput"
                          @keyup.enter.native="addKeywordInputConfirm"
                          @blur="addKeywordInputConfirm"
                        >
                        </el-input>
                        <el-button v-else type="warning" plain icon="el-icon-circle-plus" @click="showAddKeywordInput"></el-button>
                        <el-button type="primary" @click="onSubmit"></el-button>

                      </el-form-item>
                  </el-col>
              </el-row>
            </el-form>
        </search-wrap>

        <div v-if="keywordData.value !== '' && !noResult" class="detailWrap"
          v-loading="loading"
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(251, 251, 251, 0.8)">

          <div v-if="showCompare" class="moreKeywords">
            <table cellspacing="0">
              <thead>
                <tr>
                  <th width="200px"></th>
                  <th>PC</th>
                  <th>PC</th>
                  <th>PC</th>
                  <th>PC</th>
                  <th>PC</th>
                  <th>M</th>
                  <th>M</th>
                  <th>M</th>
                  <th>M</th>
                  <th>M</th>
                </tr>
              </thead>
              <tbody>
                <template v-for="item in keywordsList">
                  <tr :class="[item.value === selectedKeyword ? 'hover' : '']" @click="selectKeyword(item.value)">
                    <td>{{item.value}}</td>
                    <td>{{item.pc.overallScore}}</td>
                    <td>{{item.pc.baiduRank}}</td>
                    <td>{{item.pc.searchVolume}}</td>
                    <td>{{item.pc.promotionIndex}}</td>
                    <td>{{item.pc.includeIndex}}</td>
                    <td>{{item.m.overallScore}}</td>
                    <td>{{item.m.baiduRank}}</td>
                    <td>{{item.m.searchVolume}}</td>
                    <td>{{item.m.promotionIndex}}</td>
                    <td>{{item.m.includeIndex}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>

          <span class="keyword">{{keywordData.value}}</span>
          <el-button type="primary" plain icon="el-icon-refresh" size="mini" @click="onRefresh(keywordData.value)" :disabled="keywordData.update_time === '0000-00-00 00:00:00'"></el-button>

          <div class="clearfix">
          <div class="pc">
            <div class="ball-detail">
                <i :class="['ball', rankBallClass_pc]"></i>
              <div class="detail">
                  <p><b>PC</b><span>{{keywordData.pcData.baiduRank}}</span></p>
                  <p><b>PC</b><span>{{keywordData.pcData.searchVolume}}</span></p>
                  <p><b></b><span>{{keywordData.pcData.rankPage}}</span></p>
              </div>
            </div>
            <div class="tips">
              <p><i class="el-alert__icon el-icon-info"></i>PC</p>
              <ul>
                  <template v-for="(item, index) in keywordData.pcData.tips">
                  <li>{{index + 1}}. {{item}}</li>
                </template>
              </ul>
            </div>
            <div class="index mr">
              <span class="title">PC</span>
                <mark-block :number="keywordData.pcData.promotion.score">{{keywordData.pcData.promotion.score}}</mark-block>
              <ul>
                  <template v-for="item in keywordData.pcData.promotion.data">
                  <li @click="changeTab_PC({label: item.name})"><b>{{item.name}}</b><span :class="item.state === '' ? 'red' : ''">{{item.value}} ({{item.state}})</span></li>
                </template>
              </ul>
            </div>
            <div class="index">
              <span class="title">PC</span>
                <mark-block :number="keywordData.pcData.include.score">{{keywordData.pcData.include.score}}</mark-block>
              <ul>
                  <template v-for="item in keywordData.pcData.include.data">
                  <li @click="changeTab_PC({label: item.name})"><b>{{item.name}}</b><span :class="item.state === '' ? 'red' : ''">{{item.value}} ({{item.state}})</span></li>
                </template>
              </ul>
            </div>
              <div class="tab-table clearfix">
                <div class="buttons">
                  <el-button type="text" @click="submitInclude('pc', 'all')"></el-button>
                  <el-button type="text" style="display: none;" @click="onExport('pc')">URL</el-button>
                </div>
                <el-tabs type="border-card" @tab-click="changeTab_PC" v-model="pcTabName">
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-table
                   v-loading="loadingPCTable"
                   :data="keywordData.pcData.tableData"
                   :row-style="setRowStyle"
                   style="width: 100%">
                   <el-table-column
                     prop="url"
                     label="URL"
                     :show-overflow-tooltip="isTrue">
                   </el-table-column>
                   <el-table-column
                     prop="title"
                     label=""
                     :show-overflow-tooltip="isTrue">
                   </el-table-column>
                   <el-table-column
                     prop="number"
                     width="50px"
                     label="">
                   </el-table-column>
                   <el-table-column
                     prop="density"
                     width="50px"
                     label="">
                   </el-table-column>
                   <el-table-column
                     prop="isInclude"
                     width="75px"
                     label="">
                   </el-table-column>
                   <el-table-column label="" width="100px" header-align="center" >
                     <template slot-scope="props">
                       <el-button type="text" :disabled="props.row.isInclude === '' || props.row.isInclude === ''" @click="submitInclude('pc', props.row.id,props.row.url,pcTabName)"></el-button>
                     </template>
                   </el-table-column>
                 </el-table>
                </el-tabs>
              </div>
            </div>

          <div class="m">
            <div class="ball-detail">
                <i :class="['ball', rankBallClass_m]"></i>
              <div class="detail">
                  <p><b>M</b>{{keywordData.mData.baiduRank}}</p>
                  <p><b>M</b>{{keywordData.mData.searchVolume}}</p>
                  <p><b></b>{{keywordData.mData.rankPage}}</p>
              </div>
            </div>
            <div class="tips">
              <p><i class="el-alert__icon el-icon-info"></i>M</p>
              <ul>
                  <template v-for="(item, index) in keywordData.mData.tips">
                  <li>{{index + 1}}. {{item}}</li>
                </template>
              </ul>
            </div>
            <div class="index mr">
              <span class="title">M</span>
                <mark-block :number="keywordData.mData.promotion.score">{{keywordData.mData.promotion.score}}</mark-block>
              <ul>
                  <template v-for="item in keywordData.mData.promotion.data">
                  <li @click="changeTab_M({label: item.name})"><b>{{item.name}}</b><span :class="item.state === '' ? 'red' : ''">{{item.value}} ({{item.state}})</span></li>
                </template>
              </ul>
            </div>
            <div class="index">
              <span class="title">M</span>
                <mark-block :number="keywordData.mData.include.score">{{keywordData.mData.include.score}}</mark-block>
                <ul>
                  <template v-for="item in keywordData.mData.include.data">
                  <li @click="changeTab_M({label: item.name})"><b>{{item.name}}</b><span :class="item.state === '' ? 'red' : ''">{{item.value}} ({{item.state}})</span></li>
                  </template>
                </ul>
            </div>
            <div class="tab-table clearfix">
                <div class="buttons">
                  <el-button type="text" @click="submitInclude('m', 'all')"></el-button>
                  <el-button type="text" style="display: none;" @click="onExport('m')">URL</el-button>
                </div>
                <el-tabs type="border-card" @tab-click="changeTab_M" v-model="mTabName">
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-tab-pane key="" label="" name=""></el-tab-pane>
                  <el-table
                   id="table"
                   v-loading="loadingMTable"
                   :data="keywordData.mData.tableData"
                   :row-style="setRowStyle"
                   style="width: 100%">
                   <el-table-column
                     prop="url"
                     label="URL"
                     :show-overflow-tooltip="isTrue">
                   </el-table-column>
                   <el-table-column
                     prop="title"
                     label=""
                     :show-overflow-tooltip="isTrue">
                   </el-table-column>
                   <el-table-column
                     prop="number"
                     width="50px"
                     label="">
                   </el-table-column>
                   <el-table-column
                     prop="density"
                     width="50px"
                     label="">
                   </el-table-column>
                   <el-table-column
                     prop="isInclude"
                     width="75px"
                     label="">
                   </el-table-column>
                   <el-table-column label="" width="100px" header-align="center">
                     <template slot-scope="props">
                       <el-button type="text" :disabled="props.row.isInclude === '' || props.row.isInclude === ''" @click="submitInclude('m', props.row.id,props.row.url,mTabName)"></el-button>
                     </template>
                   </el-table-column>
                 </el-table>
                </el-tabs>
                </div>
            </div>
          </div>

        </div>

    </page-wrap>
</template>


<script>
    import PageWrap from './../../components/pageStructure/PageWrap.vue'
    import SearchWrap from './../../components/pageStructure/SearchWrap.vue'
    import TableWrap from './../../components/pageStructure/TableWrap.vue'
    import paginationWrap from './../../components/pageStructure/paginationWrap.vue'
    import PageTitle from './../../components/pageStructure/PageTitle.vue'
    import MarkBlock from './../../components/base/MarkBlock.vue'
    import { mapState } from 'vuex'

    export default {
        components: {
            PageWrap, SearchWrap, TableWrap, paginationWrap, PageTitle, MarkBlock
        },
        data () {
            return {
              searchData: {
                keyword: '',
                comapredKeyword: []
              },
              keywordData: {
                value: '',
                pcData: {},
                mData: {}
              },
              keywordsList: [],
              addKeywordInputVisible: false,
              addKeywordInputValue: '',
              showCompare: false,
              isTrue: true,
              loadingPCTable: false,
              loadingMTable: false,
              rankBallClass_pc: '',
              rankBallClass_m: '',
              selectedKeyword: '',
              loading: false,
              noResult: false,
              pcTabName: '',
              mTabName: ''
            }
        },
        computed: {
            ...mapState('navTabs',[
                'keyword_to_observe'
            ])
        },
        mounted () {
          // let contentHeight = document.getElementsByClassName('pageWrap')[0].offsetHeight
          // let table_offsetTop = document.getElementById('table')
          // console.log('contentHeight', contentHeight)
          // console.log('table_offsetTop', table_offsetTop)

          this.initSearchData()
          if (this.keyword_to_observe.keyword !== '') {
            this.onSubmit()
          }
        },
        watch: {
          keyword_to_observe () {
            this.initSearchData()
            if (this.keyword_to_observe.keyword !== '') {
              this.onSubmit()
            }
          }
        },
        methods: {
          initSearchData () {
            this.searchData = {
              keyword: this.keyword_to_observe.keyword,
              comapredKeyword: []
            }
          },
          changeTab_PC (tab) {
            this.pcTabName = tab.label
            this.loadingPCTable = true
            setTimeout(() => {
              this.loadingPCTable = false
            }, 500)
            let keyword = this.selectedKeyword
            let title = tab.label

            this.onsubmitChangeTab(keyword, title, 'pc')
            // this.keywordData.pcData.tableData = []
          },
          changeTab_M (tab) {
            this.mTabName = tab.label
            this.loadingMTable = true
            setTimeout(() => {
              this.loadingMTable = false
            }, 500)

            let keyword = this.selectedKeyword
            let title = tab.label
            this.onsubmitChangeTab(keyword, title, 'm')
          },
          onsubmitChangeTab(keyword, title, type) {
              let that = this
              this.$ajax.post('/observe/changeTab',{keyword:keyword,title:title,type:type})
                  .then(function(res) {
                      if (res.data !== undefined) {
                          if (type === 'm') {
                              that.keywordData.mData.tableData = res.data.M
                          } else {
                              that.keywordData.pcData.tableData = res.data.PC
                          }
                      }
                  })
                  .catch(function(err) {
                  })
          },
          onSubmit () {
            if (this.searchData.keyword !== '') {
            let that = this
            that.loading = true
            this.$ajax.post('/observe/index',{searchData:that.searchData})
                .then(function(res) {
                  if (res.data.type === true) {
                    that.noResult = false
                    if (res.data !== undefined) {
                      if (that.searchData.comapredKeyword.length > 0) {
                          that.showCompare = true
                          let comapredKeyword = that.searchData.comapredKeyword
                          that.keywordsList = res.data.info
                      } else {
                          that.showCompare = false
                          that.keywordData = res.data.info
                          let keyword = that.searchData.keyword
                          that.rankBallClass_pc = that.getRankBallClass(that.keywordData.pcData.overallScore)
                          that.rankBallClass_m = that.getRankBallClass(that.keywordData.mData.overallScore)
                          that.selectedKeyword = that.keywordData.value
                      }
                    }
                  } else {
                      that.noResult = true
                      that.$message({
                          type: 'info',
                          message: '!'
                      })
                  }
                  that.loading = false
                })
                .catch(function(err) {
                })
            } else {
              this.$message({
                type: 'warning',
                message: '!'
              })
            }
          },
          selectKeyword (val) {
            this.selectedKeyword = val
            this.loading = true
            let that = this
            this.$ajax.post('/observe/index',{keyword:val,type:true,searchData:that.searchData})
                .then(function(res) {
                    if (res.data.type === true) {
                        if (res.data !== undefined) {
                            that.showCompare = true
                            let comapredKeyword = that.searchData.comapredKeyword
                            that.keywordData = res.data.info
                            that.rankBallClass_pc = that.getRankBallClass(that.keywordData.pcData.overallScore)
                            that.rankBallClass_m = that.getRankBallClass(that.keywordData.mData.overallScore)
                        }
                    } else {
                        this.$message({
                            type: 'info',
                            message: '!'
                        })
                    }
                    that.loading = false
                })
                .catch(function(err) {
                  that.loading = false
                })
          },
          onRefresh (kw) {
          let that = this
          this.$ajax.get('/lexicon/refresh?kw='+kw).then(function (res) {
              that.$message({
                  type: 'success',
                  message: ""
              })
              that.onSubmit()
          })
          },
          onExport () {
          },
          getRankBallClass (type) {
            switch (type) {
              case '':
                return 'ball1'
              case '':
                return 'ball2'
              case '':
                return 'ball3'
              case '':
                return 'ball4'
              case '':
                return 'ball5'
            }
          },
          submitInclude (media, id, url,tabname) {
            this.$confirm('?', '', {
               confirmButtonText: '',
               cancelButtonText: '',
               type: 'warning'
             }).then(() => {
                let that = this
                this.$ajax.post('/observe/included',{id:id,url:url})
                    .then(function(res) {
                        if (res.data.type === true) {
                            if (res.data !== undefined) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                })
                            }
                        } else {
                            this.$message({
                                type: 'info',
                                message: '!'
                            })
                        }
                    })
                    .catch(function(err) {
                    })

                if (media === 'pc') {
                    let keyword = this.selectedKeyword
                    this.onsubmitChangeTab(keyword, tabname, 'pc')
                    this.loadingPCTable = true
                    setTimeout(() => {this.loadingPCTable = false}, 500)
                    // this.keywordData.pcData.tableData = []
                } else {
                    let keyword = this.selectedKeyword
                    this.onsubmitChangeTab(keyword, tabname, 'm')
                    this.loadingMTable = true
                    setTimeout(() => {this.loadingMTable = false}, 500)
                    // this.keywordData.mData.tableData = []
                }
             }).catch(() => {
               this.$message({
                 type: 'info',
                 message: ''
               })
             })
          },
          setRowStyle ({row}) {
            if (row.isInclude === '') {
              return { color: 'red' }
            }
          },
          showAddKeywordInput () {
            this.addKeywordInputVisible = true
            this.$nextTick(_ => {
              this.$refs.saveTagInput.$refs.input.focus()
            })
          },
          closeCompareKeyword (tag) {
            this.searchData.comapredKeyword.splice(this.searchData.comapredKeyword.indexOf(tag), 1)
          },
          addKeywordInputConfirm () {
            let addKeywordInputValue = this.addKeywordInputValue
            if (addKeywordInputValue) {
              if (addKeywordInputValue.trim() === '') {
                this.addKeywordInputVisible = false
                this.addKeywordInputValue = ''
              } else if (this.searchData.comapredKeyword.length > 3) {
                this.$message({
                  type: 'warning',
                  message: '5!'
                })
              } else if (addKeywordInputValue.trim() === this.searchData.keyword.trim() || this.searchData.comapredKeyword.indexOf(addKeywordInputValue.trim()) > -1) {
                this.$message({
                  type: 'warning',
                  message: '!'
                })
              } else {
                this.searchData.comapredKeyword.push(addKeywordInputValue)
              }
            }
            this.addKeywordInputVisible = false
            this.addKeywordInputValue = ''
          }
        }
    }
</script>

<style scoped>
  @import "./../../assets/css/page/indexObserve.css";
</style>



